import React, { Component } from "react";

class Header_admin extends Component {
  constructor(props) {
    super(props);

    this.logout = this.logout.bind(this);
  }

  logout = async () => {
    const response = await fetch(
      "http://animalseclife.site:8000/users/logout/?token=" +
        JSON.parse(localStorage.getItem("token")),
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({
          "content-type": "application/json",
        }),
      }
    );

    const rep = await response.json();
    localStorage.clear()
    window.location = "http://animalseclife.site/";
  };

  render() {
    return (
      <>
        <h3>Bienvenid@ {this.props.name}</h3>
        <h3>Lugar administrado: {this.props.site}</h3>
        <h3>Mascotas disponibles: {this.props.count_pets}</h3>
        <h3 onClick={this.logout} className="button_logout_admins">
          Logout
        </h3>
      </>
    );
  }
}

export default Header_admin;
