import React from "react";
import "../css/normalize.css";
import "../css/style.css";
import Home from "./Pages/Home";
import Login from "./Pages/Login";
import Register from "./Pages/Register";
import Perros from "./Pages/Perros";
import Gatos from "./Pages/Gatos";
import Pet from "./Pages/Pet";
import Solicitudes from "./Pages/Solicitudes";
import Cancelar_solicitud from "./Pages/Cancelar_solicitud";
import Menu_super from "./Pages/Menu_super";
import Menu_Admin from "./Pages/Menu_admin";
import Menu_staff from "./Pages/Menu_staff";
import Menu_super_usuarios from "./Pages/Menu_super_usuarios";
import Menu_admin_usuarios from "./Pages/Menu_admin_usuarios";
import Menu_super_sites from "./Pages/Menu_super_sites";
import Menu_admin_pets from "./Pages/Menu_admin_pets";
import Menu_staff_pets from "./Pages/Menu_staff_pets";
import Menu_staff_pets_delete from "./Pages/Menu_staff_pets_delete";
import Menu_staff_pets_create from "./Pages/Menu_staff_pets_create";
import Menu_staff_pets_edit from "./Pages/Menu_staff_pets_edit";
import Menu_super_sites_change from "./Pages/Menu_super_sites_change";
import Menu_super_sites_create from "./Pages/Menu_super_sites_create";
import Menu_super_usuario_delete from "./Pages/Menu_super_usuario_delete";
import Menu_admin_usuario_delete from "./Pages/Menu_admin_usuario_delete";
import Menu_admin_requests from "./Pages/Menu_admin_requests";
import Menu_staff_requests from "./Pages/Menu_staff_requests";
import Menu_staff_requests_cita from "./Pages/Menu_staff_requests_cita";
import Menu_staff_requests_rechazar from "./Pages/Menu_staff_requests_rechazar";
import Menu_staff_requests_aceptar from "./Pages/Menu_staff_requests_aceptar";
import Menu_super_usuario_create from "./Pages/Menu_super_usuario_create";
import Menu_admin_usuario_create from "./Pages/Menu_admin_usuario_create";
import Como_funciona from "./Pages/Como_funciona";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  useLocation,
} from "react-router-dom";

const App = () => (
  <>
    <Router>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/register" exact component={Register} />
        <Route path="/login" exact component={Login} />
        <Route path="/perros" exact component={Perros} />
        <Route path="/gatos" exact component={Gatos} />
        <Route path="/pet/:id" component={Pet} />
        <Route path="/solicitudes" component={Solicitudes} />
        <Route path="/Cancelar_solicitud" component={Cancelar_solicitud} />
        <Route path="/como_funciona" exact component={Como_funciona} />
        <Route path="/adminSuper" exact component={Menu_super} />
        <Route path="/adminRep" exact component={Menu_Admin} />
        <Route path="/adminStaff" exact component={Menu_staff} />
        <Route path="/adminSuperUsers" exact component={Menu_super_usuarios} />
        <Route path="/adminRepUsers" exact component={Menu_admin_usuarios} />
        <Route path="/adminSuperSites" exact component={Menu_super_sites} />
        <Route path="/adminRepPets" exact component={Menu_admin_pets} />
        <Route path="/adminRepRequests" exact component={Menu_admin_requests} />
        <Route
          path="/adminStaffRequests"
          exact
          component={Menu_staff_requests}
        />
        <Route path="/adminStaffPets" exact component={Menu_staff_pets} />
        <Route
          path="/adminSuperSites/change"
          exact
          component={Menu_super_sites_change}
        />
        <Route
          path="/adminStaffRequests/cita"
          exact
          component={Menu_staff_requests_cita}
        />
        <Route
          path="/adminStaffRequests/rechazar"
          exact
          component={Menu_staff_requests_rechazar}
        />
        <Route
          path="/adminStaffRequests/aceptar"
          exact
          component={Menu_staff_requests_aceptar}
        />
        <Route
          path="/adminSuperSites/create"
          exact
          component={Menu_super_sites_create}
        />
        <Route
          path="/adminSuperUsers/delete"
          exact
          component={Menu_super_usuario_delete}
        />
        <Route
          path="/adminRepUsers/delete"
          exact
          component={Menu_admin_usuario_delete}
        />
        <Route
          path="/adminStaffPets/delete"
          exact
          component={Menu_staff_pets_delete}
        />
        <Route
          path="/adminStaffPets/edit"
          exact
          component={Menu_staff_pets_edit}
        />
        <Route
          path="/adminStaffPets/create"
          exact
          component={Menu_staff_pets_create}
        />
        <Route
          path="/adminSuperUsers/create"
          exact
          component={Menu_super_usuario_create}
        />
        <Route
          path="/adminRepUsers/create"
          exact
          component={Menu_admin_usuario_create}
        />
      </Switch>
    </Router>
  </>
);

export default App;

