import React, { Component } from "react";
import { Link } from "react-router-dom";

class PrivateMenu extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <ul>
        <Link to="/">
          <li>INICIO</li>
        </Link>
        <Link to="/perros">
          <li>PERROS</li>
        </Link>
        <Link to="/gatos">
          <li>GATOS</li>
        </Link>
        <Link to="/como_funciona">
          <li>CÓMO FUNCIONA</li>
        </Link>
        <li>
          <button className="option_user">
            <h3>{this.props.initial}</h3>
          </button>
        </li>
      </ul>
    );
  }
}

export default PrivateMenu;
