import React, { Component, createRef } from "react";
import Header_super from "../Organisms/Header_super";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_super extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      count_users: 0,
      count_pets: 0,
      name: "Sin nombre",
      users_admin: "",
      sites_list: "",
    };
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
            });
            
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }

    const count_users = fetch("http://animalseclife.site:8000/users/count/", {
      method: "GET",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
    })
      .then((count_users) => count_users.json())
      .then((data) => {
        if (data["count"]) {
          this.setState({
            count_users: data["count"],
          });
          if (!localStorage.getItem("count_users"))
            localStorage.setItem("count_users", JSON.stringify(data["count"]));
        }
      });

    const count_pets = fetch("http://animalseclife.site:8000/pets/count/", {
      method: "GET",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
    })
      .then((count_pets) => count_pets.json())
      .then((data) => {
        if (data["count"]) {
          this.setState({
            count_pets: data["count"],
          });
          if (!localStorage.getItem("count_pets"))
            localStorage.setItem("count_pets", JSON.stringify(data["count"]));
        }
      });

    const users_admin = fetch("http://animalseclife.site:8000/users/list_admin/", {
      method: "GET",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
    })
      .then((users_admin) => users_admin.json())
      .then((data) => {
        this.setState({
          users_admin: data,
        });
        localStorage.setItem("users_admin", JSON.stringify(data));
      });

    const sites_list = fetch("http://animalseclife.site:8000/sites/list/", {
      method: "GET",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
    })
      .then((sites_list) => sites_list.json())
      .then((data) => {
        this.setState({
          sites_list: data,
        });
        localStorage.setItem("sites_list", JSON.stringify(data));
      });

    this.setState({
      name:
        JSON.parse(localStorage.getItem("user")).name +
        " " +
        JSON.parse(localStorage.getItem("user")).last_name,
    });
  }

  render() {
    const { count_pets } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    if (!JSON.parse(localStorage.getItem("user")).is_superuser) {
      if (JSON.parse(localStorage.getItem("user")).is_admin) {
        window.location = "http://animalseclife.site/adminRep";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/adminStaff";
        } else {
          window.location = "http://animalseclife.site/";
        }
      }
    }
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_super
              c_pets={count_pets}
              c_users={count_users}
              name={name}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="1" />
          <div className="main_menu_admin_content">
            <div id="contenedor_menu" className="main_menu_admin_content_con">
              <img
                src="https://i.ibb.co/56PMXWn/portada-aplicativo.jpg"
                alt=""
              />
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_super;
