import React, { Component } from "react";
import Option_aside from "../Molecules/Option_aside";

class Aside_menu extends Component {
  constructor(props) {
    super(props);

    this.administrar_menu = this.administrar_menu.bind(this);
  }

  administrar_menu() {
    switch (this.props.type) {
      case "1":
        window.location = "http://animalseclife.site/adminSuper/";
        break;
      case "2":
        window.location = "http://animalseclife.site/adminRep/";
    }
  }

  render() {
    return (
      <aside className="main_menu_admin_aside">
        <div
          className="main_menu_admin_aside_image"
          onClick={this.administrar_menu}
        >
          <img src="https://i.ibb.co/j8fFRMj/Logo-Negro.png" alt="" />
        </div>
        <div className="main_menu_admin_aside_opt">
          {(() => {
            if (this.props.type === "1") {
              return (
                <>
                  <Option_aside
                    type="1"
                    image="https://i.ibb.co/HGFDcsd/user.png"
                    text="Administrar usuarios"
                  />
                  <Option_aside
                    type="2"
                    image="https://i.ibb.co/y82G86d/marcador-de-posicion.png"
                    text="Administrar sitios"
                  />{" "}
                </>
              );
            } else {
              if (this.props.type === "2") {
                return (
                  <>
                    <Option_aside
                      type="3"
                      image="https://i.ibb.co/HGFDcsd/user.png"
                      text="Administrar personal"
                    />
                    <Option_aside
                      type="4"
                      image="https://i.ibb.co/Pm5W06z/solicitud.png"
                      text="Reporte de mascotas"
                    />
                    <Option_aside
                      type="5"
                      image="https://i.ibb.co/Pm5W06z/solicitud.png"
                      text="Reporte de solicitudes"
                    />
                  </>
                );
              } else {
                if (this.props.type === "3")
                  return (
                    <>
                      <Option_aside
                        type="6"
                        image="https://i.ibb.co/HGFDcsd/user.png"
                        text="Administrar mascotas"
                      />
                      <Option_aside
                        type="7"
                        image="https://i.ibb.co/y82G86d/marcador-de-posicion.png"
                        text="Solicitudes de adopción"
                      />{" "}
                    </>
                  );
              }
            }
          })()}
        </div>
      </aside>
    );
  }
}

export default Aside_menu;
