import React, { Component } from "react";
import Header_staff from "../Organisms/Header_staff";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_staff_pets_edit extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      pets_site: [],
      site: "Sin sitio",
      administrator: [],
    };
    this.mostrarModal = this.mostrarModal.bind(this);
    this.ocultarModal = this.ocultarModal.bind(this);
    this.actualizar = this.actualizar.bind(this);
    this.cancelar = this.cancelar.bind(this);
  }

  cancelar() {
    localStorage.removeItem("id_pet_edit");
    window.location = "http://animalseclife.site/adminStaffPets";
  }

  actualizar = async (event) => {
    event.preventDefault();
    let pet = {};
    let petsSite = JSON.parse(localStorage.getItem("pets_site"));
    petsSite.forEach((element) => {
      if (element.id_pet == localStorage.getItem("id_pet_edit")) {
        pet = element;
      }
    });
    let sitio = pet.site;
    let prioridad = 3;
    let discapacidad = "Ninguna";
    if (document.querySelector("#discapacitado").value === "Si") {
      prioridad = 1;
      discapacidad = "Si";
    } else {
      if (document.querySelector("#edad").value > 10) {
        prioridad = 2;
      }
    }

    const responde_code = document.querySelector(".response_code");
    let data = {
      type_pet: document.querySelector("#type_pet").value,
      name: document.querySelector("#name").value,
      gender: document.querySelector("#genero").value,
      breed: document.querySelector("#raza").value,
      age: document.querySelector("#edad").value,
      size: document.querySelector("#tamaño").value,
      color: document.querySelector("#color").value,
      weight: document.querySelector("#peso").value,
      personality: document.querySelector("#personalidad").value,
      description: document.querySelector("#descripcion").value,
      priority: prioridad,
      url_image: document.querySelector("#url").value,
      discapacity: discapacidad,
      site: sitio,
    };

    const response = await fetch(
      `http://animalseclife.site:8000/pets/edit/` + pet.id_pet + `/`,
      {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      }
    );
    const rep = await response.json();
    responde_code.textContent = rep["type"];
    if (rep["type"] == "1") {
      this.mostrarModal("Mascota actualizada correctamente");
      const responde_code = document.querySelector(".response_code");
      responde_code.textContent = "2";
    } else {
      if (rep["type"] == "2") {
        this.mostrarModal("Error al procesar la solicitud");
      }
    }
  };

  mostrarModal = (message) => {
    const modal = document.querySelector(".notification");
    const body = document.querySelector(".notification_body p");
    body.textContent = message;
    modal.style.display = "flex";
  };

  ocultarModal = () => {
    const responde_code = document.querySelector(".response_code");
    if (responde_code.textContent == "2") {
      window.location = "http://animalseclife.site/adminStaff/";
    }
    let modal = document.querySelector(".notification");
    modal.style.display = "none";
  };

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              administrator: JSON.parse(localStorage.getItem("administrator")),
              pets_site: JSON.parse(localStorage.getItem("pets_site")),
              site: JSON.parse(localStorage.getItem("site")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
      window.location = "http://animalseclife.site/adminSuper";
    } else {
      if (!JSON.parse(localStorage.getItem("user")).is_admin) {
        if (!JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/";
        }
      } else {
        window.location = "http://animalseclife.site/adminRep";
      }
    } 
    const { name } = this.state;
    const { site } = this.state;
    const { administrator } = this.state;
    let pet = {};
    let petsSite = JSON.parse(localStorage.getItem("pets_site"));
    petsSite.forEach((element) => {
      if (element.id_pet == localStorage.getItem("id_pet_edit")) {
        pet = element;
      }
    });
    return (
      
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_staff
              name={name}
              site={site.name}
              admin={administrator.name + " " + administrator.last_name}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="3" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Editar Mascota</h2>
              <div className="create_pet_user_form">
                <form action="" onSubmit={this.actualizar}>
                  <div className="admin_user_cont">
                    <div className="admin_user_form_column">
                      <div className="admin_user_form_input">
                        <h3>Nombre:</h3>
                        <input
                          type="text"
                          id="name"
                          defaultValue={pet.name}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Tipo de mascota:</h3>
                        <select name="" id="type_pet">
                          <option value="1">Perro</option>
                          <option value="2">Gato</option>
                        </select>
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Género:</h3>
                        <select name="" id="genero">
                          <option value="Macho">Macho</option>
                          <option value="Hembra">Hembra</option>
                        </select>
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Raza:</h3>
                        <input
                          type="text"
                          id="raza"
                          defaultValue={pet.breed}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Edad:</h3>
                        <input
                          type="number"
                          id="edad"
                          defaultValue={pet.age}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Tamaño:</h3>
                        <select name="" id="tamaño">
                          <option value="Pequeño">Pequeño</option>
                          <option value="Mediano">Mediano</option>
                          <option value="Grande">Grande</option>
                        </select>
                      </div>
                      <div className="admin_user_form_button">
                        <button type="submit">Actualizar</button>
                        <button type="submit" onClick={this.cancelar}>
                          Cancelar
                        </button>
                      </div>
                    </div>
                    <div className="admin_user_form_column">
                      <div className="admin_user_form_input">
                        <h3>Color:</h3>
                        <input
                          type="text"
                          id="color"
                          defaultValue={pet.color}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Peso:</h3>
                        <input
                          type="number"
                          id="peso"
                          defaultValue={pet.weight}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Personalidad:</h3>
                        <input
                          type="text"
                          id="personalidad"
                          defaultValue={pet.personality}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Discapacitado:</h3>
                        <select name="" id="discapacitado">
                          <option value="No">No</option>
                          <option value="Si">Si</option>
                        </select>
                      </div>
                      <div className="admin_user_form_input">
                        <h3>URL imagen:</h3>
                        <input
                          type="url"
                          id="url"
                          defaultValue={pet.url_image}
                          required
                        />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Descripción:</h3>
                        <textarea
                          name=""
                          id="descripcion"
                          cols="33"
                          rows="10"
                          defaultValue={pet.description}
                        ></textarea>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </main>
        <div className="notification">
          <div className="notification_content">
            <div className="notification_title">
              <h3>Respuesta</h3>
            </div>
            <hr />
            <div className="notification_body">
              <p></p>
              <p className="response_code" style={{ display: "none" }}></p>
            </div>
            <div className="notification_button">
              <button id="notifition_button_button" onClick={this.ocultarModal}>
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Menu_staff_pets_edit;
