import React, { Component } from "react";
import Header_admin from "../Organisms/Header_admin";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_admin_requests extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      users_staff: [],
      pets_site: [],
      site: "Sin sitio",
      requests: [],
    };
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              users_staff: JSON.parse(localStorage.getItem("users_staff")),
              pets_site: JSON.parse(localStorage.getItem("pets_site")),
              site: JSON.parse(localStorage.getItem("site")),
              requests: JSON.parse(localStorage.getItem("requests")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
      window.location = "http://animalseclife.site/adminSuper";
    } else {
      if (!JSON.parse(localStorage.getItem("user")).is_admin) {
        if (JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/adminStaff";
        } else {
          window.location = "http://animalseclife.site/";
        }
      }
    } 
    const { pets_site } = this.state;
    const { name } = this.state;
    const { site } = this.state;
    const { requests } = this.state;
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_admin
              name={name}
              site={site.name}
              count_pets={pets_site.length}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="2" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Reporte de Solicitudes</h2>
              <table
                id="table_users"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100px">Id</th>
                  <th width="200px">Fecha</th>
                  <th width="150px">Mascota</th>
                  <th width="150px">Estado</th>
                  <th width="150px">¿Está activa?</th>
                  <th width="500px">Comentarios</th>
                </tr>

                {(() => {
                  const table = document.getElementById("table_users");
                  if (table && !requests["type_response"])
                    requests.forEach((element) => {
                      let comp = element.is_active;
                      if (comp) {
                        comp = "Si";
                      } else {
                        comp = "No";
                      }
                      let coment = element.comments;
                      if (coment === null) {
                        coment = "Sin comentarios";
                      }
                      table.insertAdjacentHTML(
                        "beforeend",
                        `
                      <tr>
                        <td>${element.id_request}</td>
                        <td>${element.date}</td>
                        <td>
                            ${element.pet}
                        </td>
                        <td>${element.state}</td>
                        <td>${comp}</td>
                        <td>${coment}</td>
                      </tr>
                      `
                      );
                    });
                })()}
              </table>
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_admin_requests;
