import React, { Component } from "react";
import Header_super from "../Organisms/Header_super";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_super_usuario_create extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      count_users: 0,
      count_pets: 0,
      name: "Sin nombre",
      site: "Sin sitio",
      users_admin: "",
    };
    this.mostrarModal = this.mostrarModal.bind(this);
    this.ocultarModal = this.ocultarModal.bind(this);
    this.enviarFormulario = this.enviarFormulario.bind(this);
  }

  enviarFormulario = async (event) => {
    event.preventDefault();
    const responde_code = document.querySelector(".response_code");
    var combo = document.getElementById("type_document");
    const optionTypeDocument = combo.options[combo.selectedIndex].text;
    let data = {
      username: document.querySelector("#username").value,
      password: document.querySelector("#pass").value,
      email: document.querySelector("#email").value,
      name: document.querySelector("#name").value,
      last_name: document.querySelector("#last_name").value,
      type_document: optionTypeDocument,
      num_document: document.querySelector("#num_document").value,
      phone_number: document.querySelector("#phone_number").value,
      address: document.querySelector("#address").value,
      is_staff: true,
      is_admin: true,
      site_id: 0,
    };

    const response = await fetch("http://animalseclife.site:8000/users/add/", {
      method: "POST",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
      body: JSON.stringify(data),
    });
    const rep = await response.json();
    responde_code.textContent = rep["type_response"];
    const val = Object.values(rep);
    this.mostrarModal(val[0]);
  };

  mostrarModal = (message) => {
    const modal = document.querySelector(".notification");
    const body = document.querySelector(".notification_body p");
    body.textContent = message;
    modal.style.display = "flex";
  };

  ocultarModal = () => {
    const responde_code = document.querySelector(".response_code");
    if (responde_code.textContent) {
      window.location = "http://animalseclife.site/adminSuper/";
    } else {
      document.querySelector("#username").value = "";
      document.querySelector("#email").value = "";
      document.querySelector("#pass").value = "";
    }
    let modal = document.querySelector(".notification");
    modal.style.display = "none";
  };

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              count_users: JSON.parse(localStorage.getItem("count_users")),
              count_pets: JSON.parse(localStorage.getItem("count_pets")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    const { count_pets } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    if (!JSON.parse(localStorage.getItem("user")).is_superuser) {
      if (JSON.parse(localStorage.getItem("user")).is_admin) {
        window.location = "http://animalseclife.site/adminRep";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/adminStaff";
        } else {
          window.location = "http://animalseclife.site/";
        }
      }
    }
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_super
              c_pets={JSON.parse(localStorage.getItem("count_pets"))}
              c_users={JSON.parse(localStorage.getItem("count_users"))}
              name={
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name
              }
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="1" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Crear usuario administrador</h2>
              <div className="create_admin_user_form">
                <form action="" onSubmit={this.enviarFormulario}>
                  <div className="admin_user_cont">
                    <div className="admin_user_form_column">
                      <div className="admin_user_form_input">
                        <h3>Nombres:</h3>
                        <input type="text" id="name" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Apellidos:</h3>
                        <input type="text" id="last_name" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Tipo de documento:</h3>
                        <select name="" id="type_document">
                          <option>Cédula de ciudadanía</option>
                          <option>Cédula extranjera</option>
                        </select>
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Número de documento:</h3>
                        <input type="number" id="num_document" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Número de teléfono:</h3>
                        <input type="tel" id="phone_number" required />
                      </div>
                    </div>
                    <div className="admin_user_form_column">
                      <div className="admin_user_form_input">
                        <h3>Username:</h3>
                        <input type="text" id="username" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Contraseña:</h3>
                        <input type="password" id="pass" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Correo:</h3>
                        <input type="email" id="email" required />
                      </div>
                      <div className="admin_user_form_input">
                        <h3>Dirección:</h3>
                        <input type="text" id="address" required />
                      </div>
                    </div>
                  </div>
                  <div className="admin_user_form_button">
                    <button type="submit">Crear</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </main>
        <div className="notification">
          <div className="notification_content">
            <div className="notification_title">
              <h3>Respuesta</h3>
            </div>
            <hr />
            <div className="notification_body">
              <p></p>
              <p className="response_code" style={{ display: "none" }}></p>
            </div>
            <div className="notification_button">
              <button id="notifition_button_button" onClick={this.ocultarModal}>
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Menu_super_usuario_create;
