import React, { Component } from "react";
import Header from "../Organisms/Header";
import Modal_user from "../Molecules/Modal_user";

class Cancelar_solicitud extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: false,
    };

    this.cancelar = this.cancelar.bind(this);
    this.enviar = this.enviar.bind(this);
    this.mostrarModal = this.mostrarModal.bind(this);
    this.ocultarModal = this.ocultarModal.bind(this);
  }

  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
          }
          const user_options = document.querySelector(".option_user");
          if (user_options) {
            user_options.addEventListener("click", (e) => {
              const modal = document.querySelector(".modal_user");
              if (
                modal.style.display === "none" ||
                modal.style.display === ""
              ) {
                modal.style.display = "block";
              } else {
                modal.style.display = "none";
              }
            });
          } else {
          }
        });
    }
  }

  cancelar() {
    window.location = "http://animalseclife.site/solicitudes";
  }
  enviar() {
    const request = fetch(
      `http://animalseclife.site:8000/request/cancelar/` +
        localStorage.getItem("id_request") +
        `/`,
      {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((request) => request.json())
      .then((data) => {
        if (data["type_response"]) {
          switch (data["type_response"]) {
            case "1":
              this.mostrarModal("Solicitud cancelada correctamente");
              break;
            case "2":
              this.mostrarModal("Error al enviar la solicitud");
              break;
            case "3":
              this.mostrarModal("Error al enviar la solicitud");
              break;
          }
        }
      });
  }

  mostrarModal = (message) => {
    const modal = document.querySelector(".notification");
    const body = document.querySelector(".notification_body p");
    body.textContent = message;
    modal.style.display = "flex";
  };

  ocultarModal = () => {
    window.location = "http://animalseclife.site/solicitudes/";
  };

  render() {
    if (JSON.parse(localStorage.getItem("user"))) {
      if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (JSON.parse(localStorage.getItem("user")).is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          }
        }
      }
    }
    const { option } = this.state;
    return (
      <>
        {localStorage.getItem("user") ? (
          <Header
            word={JSON.parse(localStorage.getItem("user"))["name"].substr(0, 1)}
          />
        ) : (
          <Header word={""} />
        )}

        <main className="solicitudes">
          <div className="solicitudes_content">
            <div className="solicitudes_content_title">
              <h2>Cancelar solicitud de adopción</h2>
            </div>
            <div className="solicitudes_content_body">
              <h2>¿Está seguro que desea cancelar la solicitud de adopción?</h2>
            </div>
            <div className="solicitudes_content_buttons">
              <button onClick={this.enviar}>Estoy seguro</button>
              <button onClick={this.cancelar}>Cancelar</button>
            </div>
          </div>
        </main>
        <div className="notification">
          <div className="notification_content">
            <div className="notification_title">
              <h3>Respuesta</h3>
            </div>
            <hr />
            <div className="notification_body">
              <p></p>
              <p className="response_code" style={{ display: "none" }}></p>
            </div>
            <div className="notification_button">
              <button id="notifition_button_button" onClick={this.ocultarModal}>
                Cerrar
              </button>
            </div>
          </div>
        </div>
        {option ? (
          <Modal_user
            name={
              JSON.parse(localStorage.getItem("user"))["name"] +
              " " +
              JSON.parse(localStorage.getItem("user"))["last_name"]
            }
          />
        ) : (
          false
        )}
      </>
    );
  }
}

export default Cancelar_solicitud;
