import React, { Component } from "react";
import Header from "../Organisms/Header";
import Modal_user from "../Molecules/Modal_user";

class Como_funciona extends Component {
  constructor(props) {
    super(props);
    this.state = {
      option: false,
    };
  }

  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
            const user_options = document.querySelector(".option_user");
            if (user_options) {
              user_options.addEventListener("click", (e) => {
                const modal = document.querySelector(".modal_user");
                if (
                  modal.style.display === "none" ||
                  modal.style.display === ""
                ) {
                  modal.style.display = "block";
                } else {
                  modal.style.display = "none";
                }
              });
            }
          }
        });
    }
  }
  componentWillUnmount() {
    const { option } = this.state;
    const user_options = document.querySelector(".option_user");
    if (option) {
      user_options.removeEventListener("click", () => {});
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user"))) {
      if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (JSON.parse(localStorage.getItem("user")).is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          }
        }
      }
    }
    const { option } = this.state;
    return (
      <>
        {localStorage.getItem("user") ? (
          <Header
            word={JSON.parse(localStorage.getItem("user"))["name"].substr(0, 1)}
          />
        ) : (
          <Header word={""} />
        )}
        <main className="funcionamiento">
          <div className="funcionamiento_content">
            <img src="https://i.ibb.co/c8KmL5F/Sin-t-tulo-4.png" alt="" />
          </div>
        </main>
        {option ? (
          <Modal_user
            name={
              JSON.parse(localStorage.getItem("user"))["name"] +
              " " +
              JSON.parse(localStorage.getItem("user"))["last_name"]
            }
          />
        ) : (
          false
        )}
      </>
    );
  }
}

export default Como_funciona;
