import React, { Component } from "react";
import Header from "../Organisms/Header";
import Modal_user from "../Molecules/Modal_user";

class Pet extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: false,
      pet: [],
    };
    this.getPet = this.getPet.bind(this);
    this.adoptar = this.adoptar.bind(this);
    this.mostrarModal = this.mostrarModal.bind(this);
    this.ocultarModal = this.ocultarModal.bind(this);
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch(
        "http://animalseclife.site:8000/users/validate_token/",
        {
          method: "POST",
          mode: "cors",
          headers: new Headers({ "content-type": "application/json" }),
          body: JSON.stringify(data),
        }
      )
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
          }
        });
    }
  }

  getPet(id_pet) {
    const pets_list = fetch(
      "http://animalseclife.site:8000/pets/pet/" + id_pet + "/",
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((pets_list) => pets_list.json())
      .then((data) => {
        this.setState({
          pet: data,
        });
      });
  }

  adoptar() {
    const { pet } = this.state;
    const { option } = this.state;
    if (option == true) {
      let data = {
        applicant: JSON.parse(localStorage.getItem("user")).id,
        pet: pet.id_pet,
        site: pet.site,
      };
      const request = fetch("http://animalseclife.site:8000/request/add/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((request) => request.json())
        .then((data) => {
          if (data["type_response"]) {
            switch (data["type_response"]) {
              case "1":
                this.mostrarModal("Solicitud enviada correctamente");
                break;
              case "2":
                this.mostrarModal("Mascota no disponible");
                break;
              case "3":
                this.mostrarModal("Error al enviar la solicitud");
                break;
              case "4":
                this.mostrarModal("Ya tiene una solicitud para esta mascota");
                break;
            }
          }
        });
    } else {
      window.location = "http://animalseclife.site/login/";
    }
  }

  componentDidMount() {
    const {
      match: { params },
    } = this.props;
    this.getPet(params.id);

    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
            const user_options = document.querySelector(".option_user");
            if (user_options) {
              user_options.addEventListener("click", (e) => {
                const modal = document.querySelector(".modal_user");
                if (
                  modal.style.display === "none" ||
                  modal.style.display === ""
                ) {
                  modal.style.display = "block";
                } else {
                  modal.style.display = "none";
                }
              });
            } else {
              console.log(user_options);
            }
          }
        });
    }
  }
  mostrarModal = (message) => {
    const modal = document.querySelector(".notification");
    const body = document.querySelector(".notification_body p");
    body.textContent = message;
    modal.style.display = "flex";
  };

  ocultarModal = () => {
    window.location = "http://animalseclife.site/solicitudes/";
  };

  render() {
    if (JSON.parse(localStorage.getItem("user"))) {
      if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (JSON.parse(localStorage.getItem("user")).is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          }
        }
      }
    }
    const { pet } = this.state;
    const { option } = this.state;
    return (
      <>
        {localStorage.getItem("user") ? (
          <Header
            word={JSON.parse(localStorage.getItem("user"))["name"].substr(0, 1)}
          />
        ) : (
          <Header word={""} />
        )}
        <div className="page_pet">
          <div className="page_pet_content">
            <div className="page_pet_content_image">
              <img src={pet.url_image} alt="" />
            </div>
            <div className="page_pet_content_info">
              <div className="page_pet_content_info_title">
                <h2>{pet.name}</h2>
              </div>
              <div className="page_pet_content_info_body">
                <h3>
                  <span>Genero:</span>
                  {pet.gender}
                </h3>
                <h3>
                  <span>Raza:</span>
                  {pet.breed}
                </h3>
                <h3>
                  <span>Edad:</span>
                  {pet.age} Años
                </h3>
                <h3>
                  <span>Tamaño:</span>
                  {pet.size}
                </h3>
                <h3>
                  <span>Color:</span>
                  {pet.color}
                </h3>
                <h3>
                  <span>Peso:</span>
                  {pet.weight} Kilogramos
                </h3>
                <h3>
                  <span>Personalidad:</span>
                  {pet.personality}
                </h3>
                <h3>
                  <span>Descripción:</span>
                  {pet.description}
                </h3>
                <h3>
                  <span>Discapacidad:</span>
                  {pet.discapacity}
                </h3>
              </div>

              <div className="page_pet_content_button">
                <button onClick={this.adoptar}>Adoptar</button>
              </div>
            </div>
          </div>
        </div>
        {option ? (
          <Modal_user
            name={
              JSON.parse(localStorage.getItem("user"))["name"] +
              " " +
              JSON.parse(localStorage.getItem("user"))["last_name"]
            }
          />
        ) : (
          false
        )}
        <div className="notification">
          <div className="notification_content">
            <div className="notification_title">
              <h3>Respuesta</h3>
            </div>
            <hr />
            <div className="notification_body">
              <p></p>
              <p className="response_code" style={{ display: "none" }}></p>
            </div>
            <div className="notification_button">
              <button id="notifition_button_button" onClick={this.ocultarModal}>
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Pet;
