import React, { Component } from "react";
import Header_admin from "../Organisms/Header_admin";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_Admin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      users_staff: [],
      pets_site: [],
      site: "Sin sitio",
      requests: [],
    };
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }

    const count_users = fetch(
      `http://animalseclife.site:8000/users/list_staff/` +
        JSON.parse(localStorage.getItem("user")).site_id +
        `/`,
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((count_users) => count_users.json())
      .then((data) => {
        this.setState({
          users_staff: data,
        });

        localStorage.setItem("users_staff", JSON.stringify(data));
      });

    const count_pets = fetch(
      `http://animalseclife.site:8000/pets/list/` +
        JSON.parse(localStorage.getItem("user")).site_id +
        `/`,
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((count_pets) => count_pets.json())
      .then((data) => {
        this.setState({
          pets_site: data,
        });
        localStorage.setItem("pets_site", JSON.stringify(data));
      });

    const site_data = fetch(
      `http://animalseclife.site:8000/sites/site/` +
        JSON.parse(localStorage.getItem("user")).id +
        `/`,
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((site_data) => site_data.json())
      .then((data) => {
        this.setState({
          site: data,
        });
        localStorage.setItem("site", JSON.stringify(data));
      });

    const request_data = fetch(
      `http://animalseclife.site:8000/request/get_list_all_site/` +
        JSON.parse(localStorage.getItem("user")).site_id +
        `/`,
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
      }
    )
      .then((request_data) => request_data.json())
      .then((data) => {
        this.setState({
          requests: data,
        });
        localStorage.setItem("requests", JSON.stringify(data));
      });

    this.setState({
      name:
        JSON.parse(localStorage.getItem("user")).name +
        " " +
        JSON.parse(localStorage.getItem("user")).last_name,
    });
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
    }else{
      if(!JSON.parse(localStorage.getItem("user")).is_admin){
        if(JSON.parse(localStorage.getItem("user")).is_staff){
          window.location = "http://animalseclife.site/adminStaff";
        }else{
          window.location = "http://animalseclife.site/";
        }
      }
    } 
    
    const { pets_site } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    const { site } = this.state;
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_admin
              name={name}
              site={site.name}
              count_pets={pets_site.length}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="2" />
          <div className="main_menu_admin_content">
            <div id="contenedor_menu" className="main_menu_admin_content_con">
              <img src="https://i.ibb.co/LRRLGDJ/portada-admin.jpg" alt="" />
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_Admin;
