import React, { Component } from "react";
import Header from "../Organisms/Header";
import Modal_user from "../Molecules/Modal_user";

class Solicitudes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: false,
      requests: [],
      request: false,
    };
  }

  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
            const request = fetch(
              `http://animalseclife.site:8000/request/get_list/` +
                JSON.parse(localStorage.getItem("user")).id +
                `/`,
              {
                method: "GET",
                mode: "cors",
                headers: new Headers({ "content-type": "application/json" }),
              }
            )
              .then((request) => request.json())
              .then((data) => {
                if (!data["type_response"]) {
                  this.setState({
                    requests: data,
                    request: true,
                  });
                }
              });
          }
          const user_options = document.querySelector(".option_user");
          if (user_options) {
            user_options.addEventListener("click", (e) => {
              const modal = document.querySelector(".modal_user");
              if (
                modal.style.display === "none" ||
                modal.style.display === ""
              ) {
                modal.style.display = "block";
              } else {
                modal.style.display = "none";
              }
            });
          } else {
          }
        });
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user"))) {
      if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (JSON.parse(localStorage.getItem("user")).is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          }
        }
      }
    }
    const { request } = this.state;
    const { requests } = this.state;
    const { option } = this.state;
    return (
      <>
        {localStorage.getItem("user") ? (
          <Header
            word={JSON.parse(localStorage.getItem("user"))["name"].substr(0, 1)}
          />
        ) : (
          <Header word={""} />
        )}

        <main className="solicitudes">
          <div className="solicitudes_content">
            <div className="solicitudes_content_title">
              <h2>Solicitudes de adopción</h2>
            </div>
            <div className="solicitudes_content_table">
              <table
                id="table_users"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100px">Id</th>
                  <th width="200px">Fecha</th>
                  <th width="150px">Mascota</th>
                  <th width="150px">Estado</th>
                  <th width="500px">Comentarios</th>
                  <th width="300px">Acciones</th>
                </tr>
                {(() => {
                  const table = document.getElementById("table_users");
                  console.log(table);
                  if (table)
                    requests.forEach((element) => {
                      let coment = element.comments;
                      if (coment === null) {
                        coment = "Sin comentarios";
                      }
                      table.insertAdjacentHTML(
                        "beforeend",
                        `
                      <tr>
                        <td>${element.id_request}</td>
                        <td>${element.date}</td>
                        <td>
                            <a href="http://animalseclife.site/pet/${element.pet}"target="_blank">
                                Ver mascota
                            </a>
                        </td>
                        <td>${element.state}</td>
                        <td>${coment}</td>
                        <td>
                            <button class="solicitudes_content_table_button">
                                Cancelar solicitud
                            </button>
                        </td>
                      </tr>
                      `
                      );
                    });

                  const buttons = document.querySelectorAll(
                    ".solicitudes_content_table_button"
                  );
                  if (buttons) {
                    buttons.forEach((button) => {
                      button.addEventListener("click", (e) => {
                        const id_request =
                          e.target.parentElement.parentElement.firstElementChild
                            .textContent;
                        localStorage.setItem("id_request", id_request);
                        window.location =
                          "http://animalseclife.site/Cancelar_solicitud";
                      });
                    });
                  }
                })()}
              </table>
            </div>
          </div>
        </main>
        {option ? (
          <Modal_user
            name={
              JSON.parse(localStorage.getItem("user"))["name"] +
              " " +
              JSON.parse(localStorage.getItem("user"))["last_name"]
            }
          />
        ) : (
          false
        )}
      </>
    );
  }
}

export default Solicitudes;
