import React, { Component } from "react";
import Header_super from "../Organisms/Header_super";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_super_sites extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      count_users: 0,
      count_pets: 0,
      name: "Sin nombre",
      site: "Sin sitio",
      users_admin: "",
    };
    this.crearSitio = this.crearSitio.bind(this);
  }

  crearSitio() {
    window.location = "http://animalseclife.site/adminSuperSites/create";
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              count_users: JSON.parse(localStorage.getItem("count_users")),
              count_pets: JSON.parse(localStorage.getItem("count_pets")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    const { count_pets } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    if (!JSON.parse(localStorage.getItem("user")).is_superuser) {
      if (JSON.parse(localStorage.getItem("user")).is_admin) {
        window.location = "http://animalseclife.site/adminRep";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/adminStaff";
        } else {
          window.location = "http://animalseclife.site/";
        }
      }
    }
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_super
              c_pets={JSON.parse(localStorage.getItem("count_pets"))}
              c_users={JSON.parse(localStorage.getItem("count_users"))}
              name={
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name
              }
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="1" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Administrar Sitios</h2>
              <div className="administrar_usuarios_super_create_buttons">
                <button onClick={this.crearSitio}>Crear sitio</button>
              </div>
              <table
                id="table_sites"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100">Id</th>
                  <th>Nombre</th>
                  <th width="200">ID Administrador</th>
                  <th width="200">Mascotas disponibles</th>
                  <th width="300">Acciones</th>
                </tr>
                {(() => {
                  const table = document.getElementById("table_sites");
                  let sites_list = JSON.parse(
                    localStorage.getItem("sites_list")
                  );
                  if (table)
                    sites_list.forEach((element) => {
                      table.insertAdjacentHTML(
                        "beforeend",
                        `
                      <tr>
                        <td>${element.id_site}</td>
                        <td>${element.name}</td>
                        <td>${element.administrator}</td>
                        <td>${element.pets_availables}</td>
                        <td>
                          <button class="administrar_sitio_super_button">
                            Cambiar administrador
                          </button>
                        </td>
                      </tr>
                      `
                      );
                    });
                  const buttons = document.querySelectorAll(
                    ".administrar_sitio_super_button"
                  );
                  if (buttons) {
                    buttons.forEach((button) => {
                      button.addEventListener("click", (e) => {
                        console.log(
                          e.target.parentElement.parentElement.firstElementChild
                            .textContent
                        );
                        const id_site_change =
                          e.target.parentElement.parentElement.firstElementChild
                            .textContent;
                        localStorage.setItem("id_site_change", id_site_change);
                        window.location =
                          "http://animalseclife.site/adminSuperSites/change";
                      });
                    });
                  }
                })()}
              </table>
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_super_sites;
