import React, { Component } from "react";
import Header_staff from "../Organisms/Header_staff";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_staff_requests extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      pets_site: [],
      site: "Sin sitio",
      administrator: [],
      requests: [],
    };
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              administrator : JSON.parse(localStorage.getItem("administrator")),
              users_staff: JSON.parse(localStorage.getItem("users_staff")),
              pets_site: JSON.parse(localStorage.getItem("pets_site")),
              site: JSON.parse(localStorage.getItem("site")),
              requests: JSON.parse(localStorage.getItem("requests")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
      window.location = "http://animalseclife.site/adminSuper";
    } else {
      if (!JSON.parse(localStorage.getItem("user")).is_admin) {
        if (!JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/";
        }
      } else {
        window.location = "http://animalseclife.site/adminRep";
      }
    } 
    const { administrator } = this.state;
    const { name } = this.state;
    const { site } = this.state;
    const { requests } = this.state;
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_staff
              name={name}
              site={site.name}
              admin={administrator.name + " " + administrator.last_name}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="3" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Solicitudes de adopción</h2>
              <table
                id="table_users"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100px">Id</th>
                  <th width="200px">Fecha</th>
                  <th width="150px">Mascota</th>
                  <th width="150px">Estado</th>
                  <th width="400px">Comentarios</th>
                  <th width="400px">Acciones</th>
                </tr>

                {(() => {
                  const table = document.getElementById("table_users");
                  if (table) {
                    if (!requests["type_response"]) {
                      requests.forEach((element) => {
                        let coment = element.comments;
                        if (coment === null) {
                          coment = "Sin comentarios";
                        }
                        table.insertAdjacentHTML(
                          "beforeend",
                          `
                      <tr>
                        <td>${element.id_request}</td>
                        <td>${element.date}</td>
                        <td>
                            ${element.pet}
                        </td>
                        <td>${element.state}</td>
                        <td>${coment}</td>
                        <td>
                            <button class="button_rechazar_request">
                                Rechazar
                            </button>
                            <button class="button_aceptar_request">
                                Aceptar
                            </button>
                            <button class="button_programar_request">
                                Programar cita
                            </button>
                        
                        </td>
                      </tr>
                      `
                        );
                        const button_delete = document.querySelectorAll(
                          ".button_rechazar_request"
                        );
                        const button_aceptar = document.querySelectorAll(
                          ".button_aceptar_request"
                        );
                        const button_cita = document.querySelectorAll(
                          ".button_programar_request"
                        );

                        if (button_cita) {
                          button_cita.forEach((button) => {
                            button.addEventListener("click", (e) => {
                              const id_request =
                                e.target.parentElement.parentElement
                                  .firstElementChild.textContent;
                              localStorage.setItem(
                                "id_request_cita",
                                id_request
                              );
                              window.location =
                                "http://animalseclife.site/adminStaffRequests/cita";
                            });
                          });
                        }
                        if (button_delete) {
                          button_delete.forEach((button) => {
                            button.addEventListener("click", (e) => {
                              const id_request =
                                e.target.parentElement.parentElement
                                  .firstElementChild.textContent;
                              localStorage.setItem(
                                "id_request_rechazar",
                                id_request
                              );
                              window.location =
                                "http://animalseclife.site/adminStaffRequests/rechazar";
                            });
                          });
                        }
                        if (button_aceptar) {
                          button_aceptar.forEach((button) => {
                            button.addEventListener("click", (e) => {
                              const id_request =
                                e.target.parentElement.parentElement
                                  .firstElementChild.textContent;
                              localStorage.setItem(
                                "id_request_aceptar",
                                id_request
                              );
                              window.location =
                                "http://animalseclife.site/adminStaffRequests/aceptar";
                            });
                          });
                        }

                      }
                      );
                    }
                  }
                })()}
              </table>
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_staff_requests;
