import React, { Component } from "react";
import { Link } from "react-router-dom";

class Option_aside extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
    };
  }

  render() {
    switch (this.props.type) {
      case "1":
        return (
          <>
            <div className="main_menu_admin_aside_options">
              <a href="http://animalseclife.site/adminSuperUsers">
                <div className="main_menu_admin_aside_options_option">
                  <img src={this.props.image} alt="" />
                  <h3 className="option_name_admin">{this.props.text}</h3>
                </div>
              </a>
            </div>
          </>
        );
        break;
      case "2":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminSuperSites">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
      case "3":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminRepUsers">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
      case "4":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminRepPets">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
      case "5":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminRepRequests">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
      case "6":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminStaffPets">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
      case "7":
        return (
          <div className="main_menu_admin_aside_options">
            <a href="http://animalseclife.site/adminStaffRequests">
              <div className="main_menu_admin_aside_options_option">
                <img src={this.props.image} alt="" />
                <h3 className="option_name_admin">{this.props.text}</h3>
              </div>
            </a>
          </div>
        );
        break;
    }
  }
}

export default Option_aside;
