import React, { Component } from "react";
import { Link } from "react-router-dom";

class Card_Pet extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="card_pet">
        <div className="card_pet_content">
          <Link to={`/pet/${this.props.id}`}>
            <div className="card_pet_content_image">
              <img src={this.props.imagen} alt="" />
            </div>
          </Link>
          <div className="card_pet_content_body">
            <div className="card_pet_content_body_title">
              <h3>{this.props.nombre}</h3>
            </div>
            <div className="card_pet_content_body_info">
              <h4>
                <span>Edad: </span> {this.props.edad} años
              </h4>
              <h4>
                <span>Género: </span> {this.props.genero}
              </h4>
              <h4>
                <span>Raza: </span> {this.props.raza}
              </h4>
              <h4>
                <span>Personalidad: </span> {this.props.personalidad}
              </h4>
              <h4>
                <span>Discapadidad: </span> {this.props.discapacidad}
              </h4>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Card_Pet;
