import React, { Component } from "react";
import { Link } from "react-router-dom";

class Modal_user extends Component {
  constructor(props) {
    super(props);
    this.logout = this.logout.bind(this);
  }

  logout = async () => {
    const response = await fetch(
      "http://animalseclife.site:8000/users/logout/?token=" +
        JSON.parse(localStorage.getItem("token")),
      {
        method: "GET",
        mode: "cors",
        headers: new Headers({
          "content-type": "application/json",
        }),
      }
    );

    const rep = await response.json();
    localStorage.clear()
    window.location = "http://animalseclife.site/";
  };

  render() {
    return (
      <div className="modal_user">
        <div className="modal_user_content">
          <div className="model_user_content_name">
            <h4>{this.props.name}</h4>
          </div>
          <hr />
          <div className="model_user_content_options">
            <Link to="/solicitudes">
              <div className="model_user_content_options_option">
                <img src="https://i.ibb.co/jhnyLG7/curriculum.png" alt="" />
                <h4>Estado de solicitudes</h4>
              </div>
            </Link>
          </div>
          <div className="model_user_content_options" onClick={this.logout}>
            <div className="model_user_content_options_option">
              <img src="https://i.ibb.co/xgh9b0q/logout.png" alt="" />
              <h4>Cerrar sesion</h4>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Modal_user;
