import React, { Component } from "react";
import PrivateMenu from "../Molecules/PrivateMenu";
import PublicMenu from "../Molecules/PublicMenu";
import { Link } from "react-router-dom";

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
    };
  }

  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch(
        "http://animalseclife.site:8000/users/validate_token/",
        {
          method: "POST",
          mode: "cors",
          headers: new Headers({ "content-type": "application/json" }),
          body: JSON.stringify(data),
        }
      )
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
            });
          } else {
            this.setState({
              option: "3",
            });
          }
        });
    } else {
      this.setState({
        option: "3",
      });
    }
  }

  render() {
    const { option } = this.state;
    return (
      <header class="header">
        <div class="header__content">
          <div class="header__content__logo">
            <Link to="/">
              <img src="https://i.ibb.co/qNbc9hj/logo.png" alt="logo" />
            </Link>
          </div>
          <nav class="header__content__nav">
            {(() => {
              switch (option) {
                case "2":
                  return <PrivateMenu initial={this.props.word} />;
                case "3":
                  return <PublicMenu />;
                case "1":
                  return;
              }
            })()}
          </nav>
        </div>
      </header>
    );
  }
}

export default Header;
