import React, { Component } from "react";
import Header from "../Organisms/Header";
import Card_home from "../Organisms/Card_home";
import Footer from "../Organisms/Footer";
import Modal_user from "../Molecules/Modal_user";

class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: false,
      pets: [],
    };
  }
  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: true,
            });
      
            const user_options = document.querySelector(".option_user");
            if (user_options) {
              user_options.addEventListener("click", (e) => {
                const modal = document.querySelector(".modal_user");
                if (
                  modal.style.display === "none" ||
                  modal.style.display === ""
                ) {
                  modal.style.display = "block";
                } else {
                  modal.style.display = "none";
                }
              });
            } else {
            }
          }
        });
    }
    const pets_list = fetch("http://animalseclife.site:8000/pets/list/", {
      method: "GET",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
    })
      .then((pets_list) => pets_list.json())
      .then((data) => {
        this.setState({
          pets: data,
        });
      });
  }

  componentWillUnmount() {
    const { option } = this.state;
    const user_options = document.querySelector(".option_user");
    if (option) {
      user_options.removeEventListener("click", () => {});
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user"))){
      if (JSON.parse(localStorage.getItem("user")).is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (JSON.parse(localStorage.getItem("user")).is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (JSON.parse(localStorage.getItem("user")).is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          }
        }
      }}
    const { pets } = this.state;
    const { option } = this.state;
    return (
      <>
        {localStorage.getItem("user") ? (
          <Header
            word={JSON.parse(localStorage.getItem("user"))["name"].substr(0, 1)}
          />
        ) : (
          <Header word={""} />
        )}
        <main className="portada_home">
          <h2 className="portada_home_texto">
            <div>
              <h3>ADOPTAR</h3>
              <span>SIEMPRE SERÁ</span>
              <br></br>
              <h3>LA MEJOR OPCIÓN</h3>
            </div>
          </h2>
        </main>
        <div className="adoptar_home">
          <div className="home_card_adoptar">
            <div className="home_card_adoptar_title">
              <h2>Ellos te necesitan</h2>
            </div>
            <div className="home_card_adoptar_cards">
              {pets.map((pet) => (
                <Card_home ruta={pet.url_image} id={pet.id_pet}/>
              ))}
            </div>
          </div>
          <div className="home_razones">
            <div className="home_razones_image">
              <img
                src="https://i.ibb.co/3FDBFvR/Sin-t-tulo-4.jpg"
                alt="perro"
              />
            </div>
            <div className="home_razones_text">
              <h2>¿Por qué adoptar?</h2>
              <p>
                En la mayoría de los casos, adoptar significa darle una segunda
                oportunidad a un animal que ha sufrido un proceso de abandono, y
                en ocasiones maltrato. Acogerlo en tu casa de por vida y darle
                la estabilidad, los cuidados y el cariño que necesita va a
                ayudarlo a que recupere su confianza y su autoestima.
              </p>
            </div>
          </div>
        </div>
        <div className="portada_footer">
          <h2 className="portada_footer_texto">
            <div>
              <h3>DETRÁS DE CADA</h3>
              <div>
                <span>ANIMAL</span>
                <h3> HAY UNA</h3>
              </div>
              <span>GRAN HISTORIA</span>
            </div>
          </h2>
        </div>
        <Footer />
        {option ? (
          <Modal_user
            name={
              JSON.parse(localStorage.getItem("user"))["name"] +
              " " +
              JSON.parse(localStorage.getItem("user"))["last_name"]
            }
          />
        ) : (
          false
        )}
      </>
    );
  }
}

export default Home;
