import React, { Component } from "react";
import Header_admin from "../Organisms/Header_admin";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_admin_usuarios extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      users_staff: [],
      pets_site: [],
      site: "Sin sitio",
    };
    this.crearUsuario = this.crearUsuario.bind(this);
  }

  crearUsuario() {
    window.location = "http://animalseclife.site/adminRepUsers/create";
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              users_staff: JSON.parse(localStorage.getItem("users_staff")),
              pets_site: JSON.parse(localStorage.getItem("pets_site")),
              site: JSON.parse(localStorage.getItem("site")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
      window.location = "http://animalseclife.site/adminSuper";
    } else {
      if (!JSON.parse(localStorage.getItem("user")).is_admin) {
        if (JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/adminStaff";
        } else {
          window.location = "http://animalseclife.site/";
        }
      }
    } 
    const { pets_site } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    const { site } = this.state;
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_admin
              name={name}
              site={site.name}
              count_pets={pets_site.length}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="2" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Administrar personal</h2>
              <div className="administrar_usuarios_super_create_buttons">
                <button onClick={this.crearUsuario}>Crear personal</button>
              </div>
              <table
                id="table_users"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100">Id</th>
                  <th>Nombre</th>
                  <th>Apellido</th>
                  <th width="300">Email</th>
                  <th width="300">Acciones</th>
                </tr>
                {(() => {
                  const table = document.getElementById("table_users");
                  let usersAdmin = JSON.parse(
                    localStorage.getItem("users_staff")
                  );
                  if (table)
                    usersAdmin.forEach((element) => {
                      table.insertAdjacentHTML(
                        "beforeend",
                        `
                      <tr>
                        <td>${element.id}</td>
                        <td>${element.name}</td>
                        <td>${element.last_name}</td>
                        <td>${element.email}</td>
                        <td>
                          <button class="administrar_usuario_super_button">
                            Eliminar
                          </button>
                        </td>
                      </tr>
                      `
                      );
                    });
                  const buttons = document.querySelectorAll(
                    ".administrar_usuario_super_button"
                  );
                  if (buttons) {
                    buttons.forEach((button) => {
                      console.log("i am here");
                      button.addEventListener("click", (e) => {
                        const id_user =
                          e.target.parentElement.parentElement.firstElementChild
                            .textContent;
                        localStorage.setItem("id_user_delete", id_user);
                        window.location =
                          "http://animalseclife.site/adminRepUsers/delete";
                      });
                    });
                  }
                })()}
              </table>
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_admin_usuarios;
