import React, { Component } from "react";
import { Link } from "react-router-dom";

class Login extends Component {
  constructor(props) {
    super(props);
    this.enviarFormulario = this.enviarFormulario.bind(this);
    this.ocultarModal = this.ocultarModal.bind(this);
  }

  enviarFormulario = async (event) => {
    event.preventDefault();
    let modal = document.querySelector(".notification");
    const body = document.querySelector(".notification_body p");
    let data = {
      username: document.querySelector("#username").value,
      password: document.querySelector("#pass").value,
    };

    const response = await fetch("http://animalseclife.site:8000/users/login/", {
      method: "POST",
      mode: "cors",
      headers: new Headers({ "content-type": "application/json" }),
      body: JSON.stringify(data),
    });
    const rep = await response.json();
    if (rep["token"] && rep["user"].is_active === true) {
      localStorage.setItem("token", JSON.stringify(rep["token"]));
      localStorage.setItem("user", JSON.stringify(rep["user"]));
      if (rep["user"].is_superuser) {
        window.location = "http://animalseclife.site/adminSuper";
      } else {
        if (rep["user"].is_admin) {
          window.location = "http://animalseclife.site/adminRep";
        } else {
          if (rep["user"].is_staff) {
            window.location = "http://animalseclife.site/adminStaff";
          } else {
            window.location = "http://animalseclife.site/";
          }
        }
      }
    } else {
      body.textContent = rep["error"];
      modal.style.display = "flex";
    }
  };

  ocultarModal = () => {
    let modal = document.querySelector(".notification");
    modal.style.display = "none";
  };
  componentDidMount() {
    let rep;
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      const response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            if (JSON.parse(localStorage.getItem("user")).is_superuser) {
              window.location = "http://animalseclife.site/adminSuper";
            } else {
              if (JSON.parse(localStorage.getItem("user")).is_admin) {
                window.location = "http://animalseclife.site/adminRep";
              } else {
                if (JSON.parse(localStorage.getItem("user")).is_staff) {
                  window.location = "http://animalseclife.site/adminStaff";
                } else {
                  window.location = "http://animalseclife.site/";
                }
              }
            }
          }
        });
    }
  }

  render() {
    return (
      <>
        <main className="login_main">
          <form className="login_form" onSubmit={this.enviarFormulario}>
            <div className="login_form_image">
              <Link to="/">
                <img src="https://i.ibb.co/j8fFRMj/Logo-Negro.png" alt="" />
              </Link>
            </div>
            <div className="login_form_content">
              <input
                type="text"
                name=""
                id="username"
                placeholder="Nombre de usuario"
              />
              <input
                type="password"
                name=""
                id="pass"
                placeholder="Contraseña"
              />
              <Link to="/register">
                <h3>Registrarse</h3>
              </Link>
              <button type="submit">Login</button>
            </div>
          </form>
          <div className="login_image">
            <div className="login_image_text">
              <h2>ELLOS TAMBIÉN</h2>
              <h2>
                MERECEN <span>AMOR</span>{" "}
              </h2>
            </div>
          </div>
        </main>
        <div className="notification">
          <div className="notification_content">
            <div className="notification_title">
              <h3>Respuesta</h3>
            </div>
            <hr />
            <div className="notification_body">
              <p></p>
              <p className="response_code" style={{ display: "none" }}></p>
            </div>
            <div className="notification_button">
              <button id="notifition_button_button" onClick={this.ocultarModal}>
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Login;
