import React from "react";
import { Link } from "react-router-dom";

const PublicMenu = () => {
  return (
    <ul>
      <Link to="/">
        <li>INICIO</li>
      </Link>
      <Link to="/perros">
        <li>PERROS</li>
      </Link>
      <Link to="/gatos">
        <li>GATOS</li>
      </Link>
      <Link to="/como_funciona">
        <li>CÓMO FUNCIONA</li>
      </Link>
      <li>
        <Link to="/register">
          <button className="header__content__nav__button">REGISTRO</button>
        </Link>
      </li>
      <li>
        <Link to="/login">
          <button className="header__content__nav__button">LOGIN</button>
        </Link>
      </li>
    </ul>
  );
};

export default PublicMenu;
