import React, { Component } from "react";
import Header_staff from "../Organisms/Header_staff";
import Aside_menu from "../Organisms/Aside_menu";

class Menu_staff_pets extends Component {
  constructor(props) {
    super(props);

    this.state = {
      option: "1",
      name: "Sin nombre",
      pets_site: [],
      site: "Sin sitio",
      administrator: [],
    };
    this.crearMascota = this.crearMascota.bind(this);
  }
  crearMascota() {
    window.location = "http://animalseclife.site/adminStaffPets/create";
  }

  componentDidMount() {
    if (localStorage.getItem("token")) {
      const data = {
        token: JSON.parse(localStorage.getItem("token")),
      };
      let response = fetch("http://animalseclife.site:8000/users/validate_token/", {
        method: "POST",
        mode: "cors",
        headers: new Headers({ "content-type": "application/json" }),
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data["is_validate"] == "true") {
            this.setState({
              option: "2",
              administrator: JSON.parse(localStorage.getItem("administrator")),
              pets_site: JSON.parse(localStorage.getItem("pets_site")),
              site: JSON.parse(localStorage.getItem("site")),
              name:
                JSON.parse(localStorage.getItem("user")).name +
                " " +
                JSON.parse(localStorage.getItem("user")).last_name,
            });
          } else {
            window.location = "http://animalseclife.site/";
          }
        });
    } else {
      window.location = "http://animalseclife.site/";
    }
  }

  render() {
    if (JSON.parse(localStorage.getItem("user")).is_superuser) {
      window.location = "http://animalseclife.site/adminSuper";
    } else {
      if (!JSON.parse(localStorage.getItem("user")).is_admin) {
        if (!JSON.parse(localStorage.getItem("user")).is_staff) {
          window.location = "http://animalseclife.site/";
        }
      } else {
        window.location = "http://animalseclife.site/adminRep";
      }
    } 
    const { pets_site } = this.state;
    const { count_users } = this.state;
    const { name } = this.state;
    const { site } = this.state;
    const { administrator } = this.state;
    return (
      <>
        <header class="header_admin">
          <div className="header_admin_info">
            <Header_staff
              name={name}
              site={site.name}
              admin={administrator.name + " " + administrator.last_name}
            />
          </div>
        </header>
        <main className="main_menu_admin">
          <Aside_menu type="3" />
          <div className="main_menu_admin_content">
            <div class="administrar_usuario_super">
              <h2>Administrar mascotas</h2>
              <div className="administrar_usuarios_super_create_buttons">
                <button onClick={this.crearMascota}>Crear Mascota</button>
              </div>
              <table
                id="table_users"
                border="1"
                bordercolor="white"
                cellspacing="0"
              >
                <tr>
                  <th width="100">Id</th>
                  <th>Tipo</th>
                  <th>Nombre</th>
                  <th>Genero</th>
                  <th>Raza</th>
                  <th>Edad</th>
                  <th>Prioridad</th>
                  <th width="280">Acciones</th>
                </tr>
                {(() => {
                  const table = document.getElementById("table_users");
                  let petsSite = JSON.parse(localStorage.getItem("pets_site"));
                  if (table)
                    petsSite.forEach((element) => {
                      let type_pet = "";
                      if (element.type_pet == 1) {
                        type_pet = "Perro";
                      } else {
                        type_pet = "Gato";
                      }

                      table.insertAdjacentHTML(
                        "beforeend",
                        `
                      <tr>
                        <td>${element.id_pet}</td>
                        <td>${type_pet}</td>
                        <td>${element.name}</td>
                        <td>${element.gender}</td>
                        <td>${element.breed}</td>
                        <td>${element.age}</td>
                        <td>${element.priority}</td>
                        <td>
                        <button class="administrar_staff_eliminar_button">
                            Eliminar
                        </button>
                        <button class="administrar_staff_editar_button">
                            Editar
                        </button>
                        </td>
                      </tr>
                      `
                      );
                      const button_delete = document.querySelectorAll(
                        ".administrar_staff_eliminar_button"
                      );
                      const button_editar = document.querySelectorAll(
                        ".administrar_staff_editar_button"
                      );

                      if (button_delete) {
                        button_delete.forEach((button) => {
                          button.addEventListener("click", (e) => {
                            const id_pet =
                              e.target.parentElement.parentElement
                                .firstElementChild.textContent;
                            localStorage.setItem("id_pet_delete", id_pet);
                            window.location =
                              "http://animalseclife.site/adminStaffPets/delete";
                          });
                        });
                      }
                      if (button_editar) {
                        button_editar.forEach((button) => {
                          button.addEventListener("click", (e) => {
                            const id_pet =
                              e.target.parentElement.parentElement
                                .firstElementChild.textContent;
                            localStorage.setItem("id_pet_edit", id_pet);
                            window.location =
                              "http://animalseclife.site/adminStaffPets/edit";
                          });
                        });
                      }
                    });
                })()}
              </table>
            </div>
          </div>
        </main>
      </>
    );
  }
}

export default Menu_staff_pets;
